"use client";

import React, { useState } from "react";
import { ProductCard } from "@/components/common/Card";
import { cn } from "@/lib/utils";

interface Tab {
  id: string;
  label: string;
}

interface Product {
  id: string;
  image: string;
  category: string;
  title: string;
  price: number;
  rating: number;
  isFavorite?: boolean;
}

interface SectionProps {
  title?: string;
  subtitle?: string;
  tabs?: Tab[];
  products?: Product[];
  defaultTab?: string;
  showTabs?: boolean;
  showViewAllButton?: boolean;
  viewAllButtonText?: string;
  backgroundColor?: string;
  gridColumns?: {
    mobile?: number;
    tablet?: number;
    desktop?: number;
  };
  onTabChange?: (tabId: string) => void;
  onAddToCart?: (productId: string) => void;
  onToggleFavorite?: (productId: string) => void;
  onViewAll?: () => void;
  className?: string;
}

// Reusable Section component used for product grids with optional tabs or a View all button
export const Section: React.FC<SectionProps> = ({
  title,
  subtitle,
  tabs = [],
  products = [],
  defaultTab = "all",
  showTabs = false,
  showViewAllButton = false,
  viewAllButtonText = "View all",
  backgroundColor = "white",
  gridColumns = { mobile: 2, tablet: 2, desktop: 4 },
  onTabChange,
  onAddToCart,
  onToggleFavorite,
  onViewAll,
  className = "",
}) => {
  const [activeTab, setActiveTab] = useState(defaultTab);

  const handleTabChange = (tabId: string) => {
    setActiveTab(tabId);
    onTabChange?.(tabId);
  };

  const handleAddToCart = (productId: string) => {
    onAddToCart?.(productId);
  };

  const handleToggleFavorite = (productId: string) => {
    onToggleFavorite?.(productId);
  };

  const handleViewAll = () => {
    onViewAll?.();
  };

  const getGridClasses = () => {
    const { mobile = 2, tablet = 2, desktop = 4 } = gridColumns || {};
    const mobileClass = `grid-cols-${mobile}`;
    const tabletClass =
      tablet === 2 ? "sm:grid-cols-2" : tablet === 3 ? "sm:grid-cols-3" : "sm:grid-cols-4";
    const desktopClass = desktop === 3 ? "lg:grid-cols-3" : desktop === 4 ? "xl:grid-cols-4" : "lg:grid-cols-2";
    return `${mobileClass} ${tabletClass} lg:grid-cols-3 ${desktopClass}`;
  };

  return (
    <section className={cn("w-full py-12 lg:py-16", className)} style={{ background: backgroundColor }}>
      <div className="max-w-[1440px] mx-auto px-4 sm:px-6 lg:px-[35px]">
        {/* Title / subtitle - styling taken from LatestNews heading */}
        {title && (
          <div className="md:mb-6 mb-4 flex items-center justify-between">
            <div>
              <h2 className="font-semibold text-2xl lg:text-[28px] leading-9 tracking-[-0.02em] text-[#1D1D1D]">
                {title}
              </h2>
              {subtitle && <p className="text-[#888888] leading-9">{subtitle}</p>}
            </div>

            {/* If showTabs is false and showViewAllButton is true, render the View all button on the right */}
            {showViewAllButton && !showTabs && (
              <div className="flex-shrink-0">
                <button
                  onClick={handleViewAll}
                  className="min-w-[120px] h-[36px] px-4 bg-[#F0BA43] rounded-[40px] text-[#1D1D1D] text-sm font-medium hover:bg-[#F0BA43]/90 transition-colors shadow"
                >
                  {viewAllButtonText}
                </button>
              </div>
            )}
          </div>
        )}

        {/* Tabs (centered) */}
        {showTabs && tabs.length > 0 && (
          <div className="flex items-center justify-center gap-2 mb-8 lg:mb-12 flex-wrap">
            {tabs.map((tab) => (
              <button
                key={tab.id}
                onClick={() => handleTabChange(tab.id)}
                className={cn(
                  "min-w-[118px] h-9 px-4 rounded-[40px] text-base font-normal leading-9 tracking-[-0.02em] transition-all duration-300",
                  activeTab === tab.id
                    ? "bg-[#F0BA43] text-[#1D1D1D] shadow-[0px_10px_48px_rgba(5,6,15,0.1)]"
                    : "bg-white text-[#1D1D1D] border border-[rgba(187,187,187,0.73)]"
                )}
                style={{ fontFamily: "Inter, sans-serif" }}
              >
                {tab.label}
              </button>
            ))}
          </div>
        )}

        {/* Products grid */}
        {products && products.length > 0 && (
          <div className={`grid ${getGridClasses()} gap-4 lg:gap-[20px] mb-8`}>
            {products.map((product) => (
              <div key={product.id} className="flex justify-center">
                <ProductCard
                  id={product.id}
                  image={product.image}
                  category={product.category}
                  title={product.title}
                  price={product.price}
                  rating={product.rating}
                  isFavorite={product.isFavorite}
                  onAddToCart={() => handleAddToCart(product.id)}
                  onToggleFavorite={() => handleToggleFavorite(product.id)}
                />
              </div>
            ))}
          </div>
        )}

        {/* If tabs shown and view all requested, place view all at right */}
        {showViewAllButton && showTabs && (
          <div className="flex justify-center lg:justify-end">
            <button
              onClick={handleViewAll}
              className="min-w-[200px] h-[50px] px-8 bg-[#F0BA43] rounded-[40px] text-[#1D1D1D] text-base font-medium leading-9 tracking-[-0.02em] hover:bg-[#F0BA43]/90 transition-colors shadow-[0px_10px_48px_rgba(5,6,15,0.1)]"
              style={{ fontFamily: "Inter, sans-serif" }}
            >
              {viewAllButtonText}
            </button>
          </div>
        )}
      </div>
    </section>
  );
};

export default Section;
